import { Router } from 'express';
import {
  initiateBankLogin,
  verifyBankOtp,
  getSessionStatus,
  initiateConsent,
  handleCallback,
} from '../controllers/auth.controller.js';

const router = Router();

// POST /api/auth/initiate-login - Step 1: Send OTP
router.post('/initiate-login', initiateBankLogin);

// POST /api/auth/verify-otp - Step 2: Verify OTP
router.post('/verify-otp', verifyBankOtp);

// GET /api/auth/session - Check Session & Fetch Profile
router.get('/session', getSessionStatus);

// GET /api/auth/consent - Step 3: Redirect to Fayda
router.get('/consent', initiateConsent);

// GET /api/auth/callback - Step 4: Handle Fayda Return
router.get('/callback', handleCallback);

export default router;